import http from '/api'

export const getworksystemlist = (params = {}) =>
	http.fetchGet(`/worksystem/list`, params)

export const deleteworksystem = (params = {}) =>
	http.fetchGet(`/worksystem/delete`, params)

export const addworksystem = (params = {}) =>
	http.fetchPost(`/worksystem/add`, params, true)

export const updateworksystem = (params = {}) =>
	http.fetchPost(`/worksystem/update`, params, true)

export const getallworksystems = (params = {}) =>
	http.fetchPost(`/worksystem/listAll`, params, true)


