import http from '/api'

export const getworkshoplist = (params = {}) =>
	http.fetchGet(`/workshop/list`, params)

export const deleteworkshop = (params = {}) =>
	http.fetchGet(`/workshop/delete`, params)

export const addworkshop = (params = {}) =>
	http.fetchPost(`/workshop/add`, params, true)

export const updateworkshop = (params = {}) =>
	http.fetchPost(`/workshop/update`, params, true)

export const getallworkshops = (params = {}) =>
	http.fetchPost(`/workshop/listAll`, params, true)

export const getworktree = (params = {}) =>
	http.fetchPost(`/workshop/listTree`, params, true)

export const getequipmenttree = (params = {}) =>
	http.fetchPost(`/workshop/listEquipmentTree`, params, true)


