import React from 'react'
import { Button, Modal, Select, Input, Icon } from 'antd'
import { getworksystemlist, deleteworksystem } from '/api/worksystem'
import { getallworkshops } from '/api/workshop'
import Common from '/page/common.jsx'

import WorksystemForm from '/page/worksystem/worksystemForm.jsx'

class Worksystem extends Common{

	async componentWillMount () {
		this.getShops()
		this.search()
	}

	state = Object.assign({}, this.state, {
		search: Object.assign({}, this.state.pageconf),
		columns: [{
			title: '消防系统名称',
			dataIndex: 'name'
		},{
			title: '消防系统编号',
			dataIndex: 'code'
		},{
			title: '所在车间',
			dataIndex: 'workshopName'
		}],
		modalConf: {visible: false, item: {}},
		workshops: []
	})

	getShops = _ => {
		return getallworkshops({}).then(res => {
			this.setState({workshops: res.data})
		})
	}

	search = async _ => {
		await this.setState({loading: true, selected: {}})
		let search = Object.assign({}, this.state.search)
		return getworksystemlist(search)
		.then(res => {
			let data
			data = res.data.records.map(val => Object.assign({}, val, {key: val.id}))
			this.setState({
				tabledata: data, 
				loading: false,
				pagination: Object.assign({}, this.state.pagination, {
					total: res.data.total,
					current: res.data.current
				})
			})
		})
	}


	delete = _ => {
		this.handleOk(deleteworksystem, 'id', '删除')
	}

	done = async _ => {
		let modalConf = {visible: false, item: {}}
		await this.setState({ modalConf })
		this.research()
	}

	renderSearch = _ => <div className="mgrSearchBar">
		<Input 
		value={this.state.search.name}
		suffix={this.state.search.name ? <Icon 
		type="close-circle" 
		onClick={_ => this.changeSearch({name: ''})} /> : null}
		onChange={e => this.changeSearch({name: e.target.value})}
		style={{width: 250, marginRight: 15}}
		addonBefore="名称" placeholder="名称" />
		<Select 
		showSearch
		filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
		style={{width: 200, marginRight: 15}} 
		placeholder={`选择车间`} 
		value={this.state.search.shopId}
		onChange={val => this.changeSearch({shopId: val})}>
			{this.state.workshops.map((val, key) => <Option value={val.id} key={key}>{val.name}</Option>)}
	    </Select>
		<Button 
		onClick={this.search}
		type="primary" icon="search">搜索</Button>
		<Button 
		onClick={this.research}
		type="primary" icon="retweet">重置</Button>
	</div>

	renderBtn = _ => <div className="mgrToolBar">
		<Button 
		onClick={_ => this.addmodal('modalConf', '添加')}
		type="primary" icon="plus">添加</Button>
		<Button 
		onClick={_ => this.editmodal('modalConf', '编辑')}
		type="primary" icon="edit">修改</Button>
		<Button 
		onClick={this.changemodel}
		type="primary" icon="delete">删除</Button>
	</div>

	rendermodal = _ => [<WorksystemForm 
		onCancel={_ => this.cancelform('modalConf')}
		done={this.done}
		shops={this.state.workshops}
		config={this.state.modalConf} />,
		<Modal title="信息"
		  visible={this.state.visible}
		  onOk={this.delete}
		  mask={false}
		  width={400}
		  onCancel={this.changemodel}
		  okText="确认"
		  cancelText="取消"
		>
			<p>是否删除？</p>
		</Modal>]

}

export default Worksystem


